body {
  background: #8fb8de;
}

h1,
h3 {
  font-family: "Helvetica";
  font-weight: bold;
  color: #cdf7f6;
  animation-name: move;
  animation-duration: 6s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in-out;
  margin-left: 10%;
}

.t-1 {
  margin-top: 5%;
}
.t-2 {
  margin-top: 1%;
}
.t-3 {
  margin-top: 1%;
}

h2 {
  font-family: "Helvetica";
  font-weight: bold;
  color: #cdf7f6;
  animation-name: move1;
  animation-duration: 4s;
  animation-iteration-count: 3;
  animation-timing-function: ease-in-out;
  left: 10%;
  right: 20%;
}

.t-4 {
  position: absolute;
  bottom: 20%;
}

@keyframes move {
  from {
    margin-left: 10%;
  }
  50% {
    margin-left: 15%;
  }
  to {
    margin-left: 10%;
  }
}

@keyframes move1 {
  from {
    bottom: 20%;
  }
  40% {
    bottom: 25%;
  }
  45% {
    bottom: 20%;
  }
  50% {
    left: 30%;
    bottom: 25%;
  }
  55% {
    bottom: 20%;
  }
  60% {
    bottom: 25%;
  }
  to {
    bottom: 20%;
  }
}
